.exampleBlock {
    margin: 2em auto;
    width: calc(var(--col-size) * 8);
}

.example {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    background-color: rgba($color: #f3f3f3, $alpha: 0.5);
    padding: 1em;
	border-radius: 10px;
	margin-bottom: 2em;
}

.customDialog {
	label {
		text-transform: uppercase;
		font-size: 70%;
		font-weight: bold;
		color: #7a7a7a;

		input {
			width: 100%;
			margin: 5px 0;
			padding: 10px;
			border-radius: 5px;
			border: solid 1px #eeeeee;
		}
	}
}

.exampleLeft {
    width: 35%;
    display: flex;
    justify-content: center;
    align-items: center;

    .exampleBlock {
        display: flex;
        flex-direction: column;

        justify-content: center;
        align-items: center;
    }

    .exampleResult {
        text-align: center;
        font-weight: bold;
        text-transform: uppercase;
        font-size: 70%;
        padding-top: 10px;
        color: #7a7a7a;
    }
}

.exampleRight {
    width: 65%;

    .code {
        border-left: solid 1px rgba(0, 0, 0, 0.1);
        border-radius: 10px;
        box-shadow: 0 3px 4px rgba(0, 0, 0, 0.1);
        * {
            font-size: 85%;
            line-height: 1.2 !important;
        }
    }
}

@media screen and (max-width: 1100px) {
    .exampleBlock {
        width: calc(var(--col-size) * 10);
    }
}

@media screen and (max-width: 540px) {
    .exampleBlock {
        width: 95%;
    }
}

@media screen and (max-width: 900px) {
    .example {
        flex-wrap: wrap;
    }

    .exampleLeft {
        width: 100%;
    }

    .exampleRight {
        width: 100%;
    }
}
